import styled from '@emotion/styled';
// Local
import { useModal } from '../Hooks/useModal';
import { ModalTypes } from '../Types';

export const Toggler: ModalTypes['Parts']['Toggler'] = (props) => {
  const { children, ...rest } = props
  const { openModal } = useModal()

  return (
    <TogglerWrapper onClick={openModal} {...props}>
      {children}
    </TogglerWrapper>
  )
}

const TogglerWrapper = styled.div<ModalTypes['Props']['Toggler']>`
  color: ${({ color }) => color ?? '#000000'};
  background-color: ${(props) => props.b ?? '#007bff'};
  cursor: pointer;

  width: ${(props) => props.w ?? 'fit-content'};
  min-width: 60px;
  max-width: 300px;

  height: ${(props) => props.h ?? 'auto'};
  min-height: 30px;
  max-height: 60px;

  margin: ${(props) => props.m ?? '10px'};
  padding: ${(props) => props.p ?? '8px 16px'};

  border: none;
  border-radius: 4px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  transition: all 0.3s ease-in-out;
  &:hover {
    color: #007bff;
    background-color: white;
  }
`